
import {mapState,mapMutations,mapGetters,mapActions } from 'vuex';
import dateFormat from 'vux/src/tools/date/format.js'

export default {
name: 'user',
data () {
return {
msg: 'user',
errText:'',
stateColor:'#ff8200',
pao:0,//能否下单 0不能1能
vouShow:false,
stateText:'',
otxt:'',
ourl:''
}
},
created(){

this.getOrderinfo().then(response => {

this.orderHandle();
}).catch(err => {
this.errorEnv(err)
this.$vux.alert.show({
title: '出错提示',
content: this.errorText||'获取订单信息出错！'
});
});


this.getUserinfo().then(response => {
if(response.quotaStatus==2&&response.auditStatus==0){
this.$router.push('/userInAudit');
}
}).catch(err => {
this.errorEnv(err)
this.$vux.alert.show({
title: '出错提示',
content: this.errorText||'获取个人信息出错！'
});
});

},
filters:{
dateFormat
},
computed:{
...mapState(['errorText','authMerges','userInfo','orderInfo']),
newfinalQuota(){
return this.userInfo&&this.userInfo.finalQuota/100;
},
newAmount(){
return this.orderInfo&&this.orderInfo.amount/100
},
newPerid(){
return this.orderInfo?(this.orderInfo.merchantLoanPattern&&this.orderInfo.merchantLoanPattern.interestPerid +"期"):''
},
newtime(){
return dateFormat(this.orderInfo&&this.orderInfo.createTime, 'YYYY年MM月DD日');
}
},
methods:{
...mapMutations(['errorEnv']),
...mapActions(['getUserinfo','getOrderinfo']),
noOrder(){
const _this=this;
this.$vux.confirm.show({
title: '提示',
content: '您还没有下过单，快去我要分期下单吧！',
onCancel () {

},
onConfirm () {

_this.$router.push('/bystages');
}
})
},
orderHandle(){
if(this.orderInfo.auditState==0){
this.stateText='审核中';
this.stateColor='red';
this.ourl='/userInAudit';
}else if(this.orderInfo.auditState==2){
this.stateText='审核不通过';
this.stateColor='red';
this.ourl='/bystages';
this.otxt='再次下单'
this.pao=1;
}else{
if(this.orderInfo.confirmState==0){
if(this.orderInfo.depositState==1||(new Date().getTime()>(this.orderInfo.auditTime-0+3600*24*1000))){
this.stateText='待确认';
this.stateColor='red';
this.ourl='/confirm';
this.otxt='去确认订单'
}else{
this.stateText='待缴纳';
this.stateColor='red';
this.ourl='/deposit';
this.otxt='缴纳保证金'
}
}else{
if(this.orderInfo.loanState==0){
this.stateText='已确认';
}else if(this.orderInfo.loanState==1){
if(this.orderInfo.completeState==0){
this.stateText='还款中';
this.stateColor='blue';
this.ourl='/repayment';
this.otxt='去还款'
}else{
this.stateText='还款完成';
this.stateColor='greed';
this.ourl='/bystages';
this.otxt='再次下单'
this.pao=1;
}
}else if(this.orderInfo.loanState==2||this.orderInfo.loanState=='2'){
this.stateText='已退款';
this.stateColor='red';
this.ourl='/bystages';
this.otxt='再次下单'
this.pao=1;
}

if(this.orderInfo.depositMoney>0){
this.vouShow=true;
}
}
}
},
urlSel(){
if(this.ourl){
this.$router.push(this.ourl);
}
}
}
}



